import { useState } from "react";
import { Menu, MenuProps } from "antd";
import { Link, usePathname } from "next/link";
import { DashboardOutlined, CompassOutlined, AppstoreOutlined, WalletOutlined, DownloadOutlined, UploadOutlined, ShareAltOutlined, DollarOutlined, SettingOutlined, InfoCircleOutlined, TeamOutlined, QuestionCircleOutlined, PhoneOutlined, LogoutOutlined, MenuUnfoldOutlined, CloseOutlined } from "@ant-design/icons";
import Cookies from "js-cookie";
import { Sider } from "antd";

// Define your user and admin route maps
type RouteMap = {
  dashboard: string;
  explore?: string; // Optional, since it's for users only
  collection?: string; // Optional, since it's for users only
  wallet?: string; // Optional, since it's for users only
  deposit?: string; // Optional, since it's for users only
  withdraw?: string; // Optional, since it's for users only
  referral?: string; // Optional, since it's for users only
  commission?: string; // Optional, since it's for users only
  settings: string;
  about: string;
  team: string;
  faqs: string;
  contact: string;
  // Add admin specific routes
  usersManagement?: string; // Optional, for admin
  machineManagement?: string; // Optional, for admin
  agreementManagement?: string; // Optional, for admin
  rewardsDistribution?: string; // Optional, for admin
  referalSystemManagement?: string; // Optional, for admin
  walletManagement?: string; // Optional, for admin
  termsAndConditions?: string; // Optional, for admin
};

const userRouteMap: RouteMap = {
  dashboard: "/user/dashboard",
  explore: "/user/explore",
  collection: "/user/collection",
  wallet: "/user/wallet",
  deposit: "/user/deposit",
  withdraw: "/user/withdraw",
  referral: "/user/referral",
  commission: "/user/commission",
  settings: "/user/settings",
  about: "/about",
  team: "/team",
  faqs: "/faqs",
  contact: "/contact",
};

const adminRouteMap: RouteMap = {
  dashboard: "/admin/dashboard",
  usersManagement: "/admin/usersManagement",
  machineManagement: "/admin/machineManagement",
  agreementManagement: "/admin/agreement-management",
  rewardsDistribution: "/admin/rewards-distribution",
  referalSystemManagement: "/admin/referral-system-management",
  walletManagement: "/admin/wallet-management",
  settings: "/admin/settings",
  faqs: "/faqs",
  termsAndConditions: "/contact",
  contactInformation: "/contact",
};

const Sidebar = () => {
  const [collapsed, setCollapsed] = useState(false);
  const role = Cookies.get("role") || "USER"; // Assume you are storing role as a cookie
  const routeMap = role === "ADMIN" ? adminRouteMap : userRouteMap;

  const pathname = usePathname();
  const selectedKey =
    Object.entries(routeMap).find(([, path]) => pathname.startsWith(path))?.[0] || "dashboard";

  const handleClick: MenuProps["onClick"] = ({ key }) => {
    if (key === "logout") {
      // handle logout logic
    }
    setCollapsed(true);
  };

  // Define the menuItems based on your routeMap
  const menuItems = [
    { key: "dashboard", icon: <DashboardOutlined />, label: <Link href={routeMap.dashboard}>Dashboard</Link> },
    ...(routeMap.explore ? [{ key: "explore", icon: <CompassOutlined />, label: <Link href={routeMap.explore}>Explore</Link> }] : []),
    ...(routeMap.collection ? [{ key: "collection", icon: <AppstoreOutlined />, label: <Link href={routeMap.collection}>My Collection</Link> }] : []),
    ...(routeMap.wallet ? [{ key: "wallet", icon: <WalletOutlined />, label: <Link href={routeMap.wallet}>Wallet</Link> }] : []),
    ...(routeMap.deposit ? [{ key: "deposit", icon: <DownloadOutlined />, label: <Link href={routeMap.deposit}>Deposit</Link> }] : []),
    ...(routeMap.withdraw ? [{ key: "withdraw", icon: <UploadOutlined />, label: <Link href={routeMap.withdraw}>Withdraw</Link> }] : []),
    ...(routeMap.referral ? [{ key: "referral", icon: <ShareAltOutlined />, label: <Link href={routeMap.referral}>Referral</Link> }] : []),
    ...(routeMap.commission ? [{ key: "commission", icon: <DollarOutlined />, label: <Link href={routeMap.commission}>Referral Commission</Link> }] : []),
    { key: "settings", icon: <SettingOutlined />, label: <Link href={routeMap.settings}>Settings</Link> },
    {
      type: "group", label: "MORE", children: [
        { key: "about", icon: <InfoCircleOutlined />, label: <Link href={routeMap.about}>About Us</Link> },
        { key: "team", icon: <TeamOutlined />, label: <Link href={routeMap.team}>Team</Link> },
      ],
    },
    {
      type: "group", label: "Links", children: [
        { key: "faqs", icon: <QuestionCircleOutlined />, label: <Link href={routeMap.faqs}>FAQs</Link> },
        { key: "contact", icon: <PhoneOutlined />, label: <Link href={routeMap.contact}>Contact Info</Link> },
      ],
    },
    { key: "logout", icon: <LogoutOutlined />, label: <span>Logout</span> },
  ];

  return (
    <>
      {/* Mobile Toggle Button */}
      <button
        className="md:hidden fixed top-6 left-6 z-50 bg-[#161616] text-white p-2 rounded"
        onClick={() => setCollapsed(!collapsed)}
      >
        {collapsed ? <MenuUnfoldOutlined /> : <CloseOutlined />}
      </button>

      {/* Mobile Sidebar */}
      <div
        className={`fixed top-0 left-0 h-screen w-[220px] bg-[#161616] z-40 transition-transform duration-300 md:hidden ${collapsed ? "-translate-x-full" : "translate-x-0"}`}
      >
        <div className="h-16 grid place-items-center text-white font-bold border-b border-gray-700">
          LOGO
        </div>
        <div className="overflow-y-auto h-[calc(100vh-64px)] scrollbar-hide">
          <Menu
            mode="inline"
            selectedKeys={[selectedKey]}
            theme="dark"
            items={menuItems}
            className="!bg-[#161616]"
            onClick={handleClick}
          />
        </div>
      </div>

      {/* Desktop Sidebar */}
      <div className="hidden md:block sticky top-0 h-screen w-[220px]">
        <Sider theme="dark" className="!bg-[#161616] h-screen overflow-hidden" width={220}>
          <div className="h-16 grid place-items-center text-white font-bold border-b border-gray-700">
            LOGO
          </div>
          <div className="overflow-y-auto h-[calc(100vh-64px)] scrollbar-hide">
            <Menu
              mode="inline"
              selectedKeys={[selectedKey]}
              theme="dark"
              items={menuItems}
              className="!bg-[#161616]"
            />
          </div>
        </Sider>
      </div>
    </>
  );
};

export default Sidebar;
